import "@arco-design/web-react/dist/css/arco.css";
import React, { useState, useEffect } from "react";
import { Grid } from "@arco-design/web-react";
import {
  List,
  Modal,
  Avatar,
  Select,
  Message,
  Space,
  Input,
} from "@arco-design/web-react";
import { Tabs, Typography } from "@arco-design/web-react";
const TabPane = Tabs.TabPane;
const style = {
  textAlign: "center",
  marginTop: 20,
};
const Option = Select.Option;

const Row = Grid.Row;
const Col = Grid.Col;

function Bussearch() {
  const [busdata, setbusdata] = useState([]); //所有公車路線
  const [serchbuslistshow, setserchbuslistshow] = useState([]); //顯示公車路線
  const [serchbusnamefilter, setserchbusnamefilter] = useState([]); //路線名稱篩選後array
  const [serchbusfiltercountry, setserchbusfiltercountry] = useState([]); //路線名稱篩選後array

  const [showebusmodal, setshowebusmodal] = useState(false); //公車動態modal

  useEffect(() => {
    
  }, []);
  const options = [
    "公路客運",
    "基隆市",
    "台北市",
    "新北市",
    "桃園市",
    "台中市",
    "台南市",
    "高雄市",
    "新竹市",
    "新竹縣",
    "苗栗縣",
    "彰化縣",
    "南投縣",
    "雲林縣",
    "嘉義縣",
    "嘉義市",
    "屏東縣",
    "宜蘭縣",
    "花蓮縣",
    "台東縣",
    "金門縣",
    "澎湖縣",
    "連江縣",
  ];
  const [activeTab, setActiveTab] = useState("1");
  const [DepartureStopNameZh, SetDepartureStopNameZh] = useState("起點");
  const [DestinationStopNameZh, SetDestinationStopNameZh] = useState("迄點");

  const routerserch = (e) => {
    console.log(e);
    if (serchbusfiltercountry.length == 0) {
      console.log(e);
      var result = busdata.filter((serchbuslist) =>
        serchbuslist.RouteName.includes(e)
      );
      setserchbusnamefilter(result);
      setserchbuslistshow(result);
    } else {
      routerfilter(serchbusfiltercountry);
    }
  };

  const routerfilter = (e) => {
    setserchbusfiltercountry(e);
    if (e.length == 0) {
      setserchbuslistshow(serchbusnamefilter);
    } else {
      var result = serchbusnamefilter.filter((serchbuslist) =>
        e.includes(serchbuslist.TypeE)
      );
      setserchbuslistshow(result);
    }
  };

  
  return (
    <div style={{ width: "100%" }}>
      <Input
        allowClear
        placeholder="請輸入路線編號"
        onChange={(e) => routerserch(e)}
      />
      <Select
        showSearch
        mode="multiple"
        options={options}
        defaultValue={[]}
        placeholder="篩選地區"
        onChange={(e) => routerfilter(e)}
      />
      <List
        className="list-demo-action-layout"
        bordered={false}
        dataSource={serchbuslistshow}
        render={(item, index) => (
          <List.Item
            onClick={(e) => setshowebusmodal(true)}
            key={index}
            style={{
              borderBottom: "1px solid var(--color-fill-3)",
              marginLeft: "10px",
              marginReft: "10px",
            }}
            actionLayout="vertical"
            extra={
              <div className="image-area">
                <h5>{item.TypeE}</h5>
              </div>
            }
          >
            <List.Item.Meta
              title={item.RouterHeadSign}
              description={item.HeadSign}
            />
          </List.Item>
        )}
      />
      <Modal
        alignCenter={false}
        title="Modal Title"
        style={{
          borderBottom: "1px solid var(--color-fill-3)",
          height: "80%",
          width: "80%",
        }}
        visible={showebusmodal}
        autoFocus={false}
        focusLock={true}
        footer={null}
      >
        <Tabs activeTab={activeTab} onChange={setActiveTab}>
          <TabPane key="1" title={DepartureStopNameZh}>
            <Typography.Paragraph style={style}>
             <h6>123</h6>
            </Typography.Paragraph>
          </TabPane>
          <TabPane key="2" title={DestinationStopNameZh}>
            <Typography.Paragraph style={style}>
              Content of Tab Panel 2
            </Typography.Paragraph>
          </TabPane>
          
        </Tabs>
      </Modal>
    </div>
  );
}

export default Bussearch;
